.product {
  display: flex;
  flex-direction: column;
  max-width: $moyoColor;
  height: 403px;
  padding: 30px 14px 14px;
  border: 1px solid $borderProductColor;
  border-radius: 5px;
  width: $cardWidth;

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 5px;
    transition: 1s;
    box-sizing: content-box;

    &:hover {
      transform: scale(1.2) translateY(1px);
      padding: 15px 0;
      margin: -15px auto;
      outline: 3px solid $borderProductColor;
    }
  }

  &__description {
    font-size: 12px;
    font-weight: 500;
    line-height: 15px;
    margin: 45px 0 5px;
  }

  &__code {
    @include smallText;
    margin: 0 0 15px;
  }

  &__rating {
    height: 15px;
    margin: 0 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__feedbacks {
    @include smallText;
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 0 0 20px;
  }

  &__price-label {
    @include smallText;
    margin: 0;
  }

  &__price {
    font-weight: 900;
    font-size: 16px;
    line-height: 19px;
  }

  &__button {
    display: inline-block;
    width: 170px;
    height: 40px;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    line-height: 40px;
    background-color: $moyoColor;
    text-align: center;
    border-radius: 5px;
    border: 1px solid $moyoColor;
    transition: 1s;

    &:hover {
      color: $moyoColor;
      background-color: #fff;
    }
  }
}
